@import url(https://rsms.me/inter/inter.css);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

html { font-family: 'Inter', sans-serif; }
@supports (font-variation-settings: normal) {
  html { font-family: 'Inter var', sans-serif; }
}

@font-face {
  font-family: 'AvenirBlack';
  src: url(/ekang98/static/media/AvenirLTStd-Black.7ce894e1.otf);
}
@font-face {
  font-family: 'AvenirMedium';
  src: url(/ekang98/static/media/Avenir_Medium.54a775d7.ttf);
}
@font-face {
  font-family: 'Circular';
  src: url("/ekang98/static/media/Circular Std Medium.3fcb6535.ttf");
}

.homepage-container {
  height: 100vh;
  width: 100vw;
  display: flex;
  justify-content: center;
}

.bio-container {
  display: flex;
  flex-direction: column;
  width: 60%;
  margin-top: 175px;
  color: #5C7296;
}

.header-container {
  display: flex;
  flex-direction: row;
}

.header {
  font-family: 'AvenirBlack';
  font-size: 42px;
  margin-bottom: 5px;
}

.surfboard {
  background-image: url(/ekang98/static/media/surfboard.af1d5aaa.png);
  background-size: contain;
  width: 37px;
  height: 37px;
  margin-left: 15px;
}

.bio {
  font-family: 'AvenirMedium';
  font-size: 24px;
  line-height: 33px;
}

.links-container {
  margin-top: 20px;
  display: flex;
  flex-direction: row;
}

.link {
  font-family: 'Circular';
  font-size: 26px;
  margin-right: 20px;
  color: #5C7296;
  text-decoration: none;
}

.link:hover {
  text-decoration: underline;
}

